@mixin fill-full($dir: 'full', $type: absolute) {
  position: $type;

  @if $dir != 'bottom' { top: 0; }

  @if $dir != 'right' { left: 0; }

  @if $dir != 'left' { right: 0; }

  @if $dir != 'top' { bottom: 0; }
}

.cat {
  position: relative;
  width: 100%;
  max-width: 20em;
  overflow: hidden;
  background-color: $color_base;

  &::before {
    display: block;
    padding-bottom: 100%;
    content: '';
  }

  &:hover > * { animation-play-state: paused; }
  &:active > * { animation-play-state: running; }
}

%cat-img {
  @include fill-full;

  animation: rotating 2.79s cubic-bezier(0.65, 0.54, 0.12, 0.93) infinite;

  &::before {
    position: absolute;
    width: 50%;
    height: 50%;
    content: '';
    background-image: url('../images/cat.png');
    background-repeat: no-repeat;
    background-size: 200%;
  }
}

.cat__head {
  @extend %cat-img;

  &::before {
    top: 0;
    right: 0;
    background-position: 100% 0%;
    transform: rotate(90deg);
    transform-origin: 0% 100%;
  }
}

.cat__tail {
  @extend %cat-img;

  animation-delay: 0.2s;

  &::before {
    bottom: 0;
    left: 0;
    background-position: 0% 100%;
    transform: rotate(-30deg);
    transform-origin: 100% 0%;
  }
}

.cat__body {
  @extend %cat-img;

  animation-delay: 0.1s;

  &:nth-of-type(2) {
    animation-delay: 0.2s;
  }

  &::before {
    right: 0;
    bottom: 0;
    background-position: 100% 100%;
    transform-origin: 0% 0%;
  }
}

@keyframes rotating {
  from { transform: rotate(720deg); }
  to { transform: none; }
}

.loading-box {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  flex: 1;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  justify-content: center;
  width: 100%;
  height: 100vh;
  background-color: $color_base;
}

.loading-box.loaded {
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s;
}
